
import { defineComponent, reactive, getCurrentInstance, ref } from 'vue'
import { Request } from '@/request/request'
import { useMdTransform } from './hooks'
import { StateType } from './type'
export default defineComponent({
    async setup() {
        // console.log(typeof md)
        const vm = getCurrentInstance()
        const state = reactive<StateType>({
            htmlArray: []
        })
        try {
            const { data } = await Request('get', '/md')
            const htmlArray = useMdTransform(data.result)
            state.htmlArray.push(...htmlArray)
        } catch (e) {
            console.log(e)
        }

        return {
            state
        }
    }
})
